import {Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogComponent} from './dialog/dialog.component';
import {Sheet800Service} from './sheet800.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {ConfirmationDialogComponent} from './confirmation-dialog/confirmation-dialog.component';
import { ReportService } from 'src/app/pages/generate-report/report.service';
import { ReportData } from 'src/app/data/report-data';
import { Location } from '@angular/common';

// @ts-ignore
@Component({
  selector: 'app-sheet800',
  templateUrl: './sheet800.component.html',
  styleUrls: ['./sheet800.component.css']
})
export class Sheet800Component implements OnInit {

  dataSource: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'code', 'description', 'demand',
    'saving', 'up_to_90_days', 'schedule_91_to_180_days', 'schedule_181_to_365_days', 'over_365_days'];
  confirmDelete: boolean;
  reportData: ReportData;
  reportSelectedDate: string;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private dialog: MatDialog,
    private sheet800Service: Sheet800Service,
    private reportService: ReportService,
    protected readonly location: Location) { }


  ngOnInit(): void {
    this.getAllMDFIR800Data();
    this.reportService.getTableData().forEach((e) => {
      if (e.return_code === 'MDFIR800'){
       this.reportData = e;
      }
      });

      this.reportService.selectedDate.subscribe((date) => {
        this.reportSelectedDate = date;
      });
    }
  // tslint:disable-next-line:typedef

  openDialog(): void {
    this.dialog.open(DialogComponent, {
       width: '30%',
    }).afterClosed().subscribe(value => {
      if (value === 'save'){this.getAllMDFIR800Data();
      }
    });
  }

  goBack() {
    this.location.back();
  }

  getAllMDFIR800Data(): void {
    this.sheet800Service.getMDFIR800Data().subscribe({
      next: (res) => {
        console.log(res.sheet800);
        this.dataSource = new MatTableDataSource(res.sheet800);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, error: () => {console.log('Error while fetching data');
      }
    });
  }

  // tslint:disable-next-line:typedef
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  editReportRow(row: any): void {
    this.dialog.open(DialogComponent, {width: '30%', data: row}).afterClosed().subscribe(value => {
      if (value === 'update'){
        this.getAllMDFIR800Data();
      }
    });
  }

  deleteReportRow(id: any): void {
    this.sheet800Service.deleteMDFIR800Data(id).subscribe({
      next: value => {
        alert('Deleted sucessfully');
        this.getAllMDFIR800Data();
      }, error: err => {
        alert('Failed');
      }
    });
  }

  // tslint:disable-next-line:typedef
  openConfirmationDialog(id) {

    this.dialog.open(ConfirmationDialogComponent, {
      width: '17%',
      data: true
    }).afterClosed().subscribe(value => {
      if (value === 'delete'){
        this.deleteReportRow(id);
        this.getAllMDFIR800Data();
      }
    });
  }
}
